@import url(https://fonts.googleapis.com/css2?family=Saira+Condensed:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
@font-face {
  font-family: 'HelveticaNeue-Local';
  src: local('HelveticaNeue-Local'), url(/static/media/HelveticaNeue.429952c9.ttf) format('truetype');
}

@font-face {
  font-family: 'HelveticaNeue-CondensedBlack-Local';
  src: local('HelveticaNeue-CondensedBlack-Local'), url(/static/media/HelveticaNeue-CondensedBlack.ca891c1d.ttf) format('truetype');
}

@font-face {
  font-family: 'HelveticaNeue-Light-Local';
  src: local('HelveticaNeue-Light-Local'), url(/static/media/HelveticaNeue-Light.8c1c8f8e.ttf) format('truetype');
}

body {
    margin: 0;
    padding: 0;
    color: #202839;
    background: #c3375e;
}

a {
    text-decoration: none;
}

.App {
    margin: 0;
}

.Caption {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-CondensedBlack-Local', 'HelveticaNeue-CondensedBlack', 'Helvetica Neue Condensed Black', 'Helvetica Neue', 'Saira Condensed', Helvetica;
    font-size: 80px;
    font-weight: 900;
    line-height: 72px;
    font-stretch: condensed;
    text-transform: uppercase;
}

.Caption-Description {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 24px;
    line-height: 36px;
    font-weight: 400;
    max-width: 760px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.Caption-Description-Extended {
    margin: 0;
    padding: 0;
    color: #ffffff;
    font-family: 'HelveticaNeue-Light-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 24px;
    line-height: 36px;
    font-weight: 300;
    max-width: 800px;
    margin-top: 0px;
    margin-bottom: 0px;
    opacity: 0.88;
}

.Caption-Description-Extended a {
    color: #c3375e;
}

.Caption-Description-Extended a:hover {
    color: #c3375e;
}

.Links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: baseline;
            align-items: baseline;
    margin-top: -16px;
}

.Links-Group-Header {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 24px;
    line-height: 36px;
    font-weight: 400;
}

.Links-Group {
    margin-top: 64px;
}

.Links-Group:first-child {
    margin-top: 0;
}

.Links-Group-Content {
    margin: 0;
    padding: 0;
}

.Container-Red {
    background: #c3375e;
    padding: 64px;
    margin: 0;
}

.Container-Blue {
    background: #202839;
    padding: 64px;
    margin: 0;
}

@media only screen
       and (min-width: 1280px) {

    .Container-Red {
        padding-left: 20vw;
    }

    .Container-Blue {
        padding-left: 20vw;
    }
}

.Caption-Description {
    margin-top: 16px;
}

@media only screen
       and (max-width: 640px) {

    .Caption {
        font-size: 48px;
        line-height: 40px;
    }

    .Container-Red, .Container-Blue {
        padding: 32px;
    }

    .Links-Group-Header, .Caption-Description, .Caption-Description-Extended {
        font-size: 16px;
        line-height: 22px;
    }

    .Caption-Description {
        margin-top: 16px;
    }
}


.Link {
    display: -webkit-flex;
    display: flex;
    margin: 0;
    padding: 0;
    width: 100%;
    min-width: 80vw;
    margin-left: -4px;
}

.Link a {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin: 0;
    padding: 0;
    display: flex;
    margin-top: 8px;
    transition: 0.64s;
    height: 80px;
    padding-top: 8px;
    padding-bottom: 8px;
}

.Link a:hover {
    height: 96px;
    margin-left: -8px;
    padding-top: 0px;
    padding-bottom: 0px;
    transition: 0.32s;
}

.Link-Image {
    margin: 0;
    padding: 0;
    height: 100%;
    margin-right: 12px;
}

.Link-Content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    padding: 0;
    padding-top: 2px;
    padding-bottom: 2px;
}

.Link-Content-Caption {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 28px;
    line-height: 28px;
    font-weight: 400;
    text-decoration: none;
}

.Link-Content-Description {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-Light-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 20px;
    line-height: 20px;
    font-weight: 300;
    text-decoration: none;
}

@media only screen
       and (max-width : 640px) {

    a {
        height: 56px;
        padding-top: 4px;
        padding-bottom: 4px;
    }

    a:hover {
        height: 56px;
        padding-top: 4px;
        padding-bottom: 4px;
        margin-left: 0;
    }

    .Link-Image {
        margin-right: 8px;
    }

    .Link-Content-Caption {
        font-size: 20px;
        line-height: 20px;
    }

    .Link-Content-Description {
        font-size: 16px;
        line-height: 16px;
    }
}

